import moment from "moment/moment"

export function getAGcode() {
    let url = new URL(window.location.href),
        params = new URLSearchParams(url.search),
        agTitle = url.search.split('=')[0].split('?')[1],
        agValue = params.getAll(`${agTitle}`)[0]
    if (agTitle == undefined || agValue == undefined) {
        return
    } else {
        if (ifStrHave(agTitle.toLowerCase(), 'agcode')) {
            if (agValue.length > 10) {
                sessionStorage.setItem('agcode', window.atob(agValue))
            } else {
                sessionStorage.setItem('agcode', agValue)
            }
        }
    }
    return agValue
}

export function toCurrency(num) { //
    if (num === undefined || num === null) {
        return
    } else {
        const parts = num.toString().split('.');
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        return parts.join('.');
    }
}

export function limitYears(date, years) { //
    var res = new Date(date);
    res.setFullYear(res.getFullYear() - years); //
    return res;
}

export function limitNumber(val) { // input 
    val = val.replace(/[^\d]/g, '');
    return val
}

export function ifStrHave(str, specifyText) { // str:,specifyStr:
    if (str.indexOf(specifyText) !== -1) {
        return true
    } else {
        return false
    }
}

export function goAnchor(id) { //
    document.getElementById(id).scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'nearest' })
}

export function timer(btnId, id, seconds) {
    var btn = document.getElementById(btnId);
    var ele = document.getElementById(id);
    var num = Number(seconds);
    btn.disabled = true;
    var timer = null;
    timer = setInterval(function () {
        if (num == -1) {
            clearTimeout(timer);
            btn.disabled = false;
            num = Number(seconds); //
        }
        else {
            ele.innerHTML = num;
            num--;
        }
    }, 1000);
}

export function dateChange(type, day) { //
    let dayDay = day.split(' ')
    let newGroupDay
    if (dayDay[1] === '') {
        newGroupDay = `${dayDay[0]} ${dayDay[2]} am`
    } else if (dayDay[1] === '') {
        newGroupDay = `${dayDay[0]} ${dayDay[2]} pm`
    }
    if (type === 'UTC') {
        return new Date(newGroupDay).toUTCString()
    } else if (type === 'taiwan') {
        return new Date(newGroupDay)
    }
}

export function DateDiff(sDate1, sDate2) { // sDate1  sDate2  2016-06-18 
    var oDate1 = new Date(sDate1);
    var oDate2 = new Date(sDate2);
    var iDays = parseInt(Math.abs(oDate1 - oDate2) / 1000 / 60 / 60 / 24); // 
    return iDays;
};

//* type(:name / num)
export function hideCenterText(type, str) { //
    if (type == 'name') {
        let newStr;
        if (str.length === 2) {
            newStr = str.substr(0, 1) + 'O'
        } else if (str.length > 2) {
            let char = '';
            for (let i = 0, len = str.length - 2; i < len; i++) {
                char += 'O'
            }
            newStr = str.substr(0, 1) + char + str.substr(-1, 1)
            // +  + 
        }
        return newStr
    } else if (type == 'num') {
        if (str == null || str == '' || str == undefined) {
            return
        } else {
            return str.replace(/^(.{3})(?:\d+)(.{3})$/, "$1****$2")
        }
    }
}

