//* 
export function randomArray(arr) {
    let newArr = []; //new ary
    arr.forEach((item, index) => {
        newArr.push(item)
    })
    // newArr  random
    for (var i = newArr.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1)); //random index
        [arr[i], arr[j]] = [arr[j], arr[i]]; // swap
    }
}

