export function isHaveNumOrEnglish(text) {
    let enText = new RegExp("[A-Za-z]+");
    let numText = new RegExp("[0-9]+");
    if (enText.test(text) && numText.test(text)) { //
        return true
    } else {
        return false
    }
}

